import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { a11yDark } from "react-syntax-highlighter/dist/esm/styles/prism";
import { CodeArray } from "./components/CodeSnippets";
import "./App.css";
import Particles from "react-particles-js";
import React, { useState } from "react";
import {
  FacebookOutlined,
  InstagramOutlined,
  LinkedinOutlined,
} from "@ant-design/icons";
import { Progress } from "antd";
import { useEffect } from "react";

function App() {
  const [percent, setPercent] = useState<number>(0);
  const [selectedCode, setSelectedCode] = useState<number>(
    Math.floor(Math.random() * 4 + 1)
  );

  let interval: any;
  let i: number = 0;

  const startTimer = () => {
    interval = setInterval((percent) => {
      setPercent(i);
      i += 9.5;
      if (i >= 100) {
        setSelectedCode(Math.floor(Math.random() * 4 + 1));
        setPercent(0);
        i = 0;
      }
    }, 1000);
  };

  useEffect(() => {
    startTimer();
  }, []);

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        height: "100vh",
        alignItems: "center",
        justifyContent: "center",
        paddingTop: 32,
      }}
    >
      <div style={{ position: "absolute" }}>
        <Particles
          width="97vw"
          height="97vh"
          params={{
            particles: {
              move: { speed: 1 },
              color: {
                value: "#333333",
              },
              line_linked: {
                color: {
                  value: "#00b8e6",
                },
              },
              number: {
                value: window.innerWidth > 600 ? 60 : 14,
              },
              size: {
                value: 2,
              },
            },
            interactivity: {
              events: {
                onhover: {
                  enable: true,
                  mode: "repulse",
                },
              },
            },
          }}
        />
      </div>

      <div
        style={{
          display: "flex",
          flexWrap: "wrap",
          flexDirection: "column",
        }}
      >
        <div
          style={{
            display: "flex",
            flexGrow: 1,
            flexWrap: "wrap",
            justifyContent: "center",
          }}
        >
          <div
            style={{
              paddingLeft: 32,
              paddingRight: 64,
              zIndex: 9999,
            }}
          >
            <h1
              style={{
                fontSize: "3em",

                paddingRight: 8,
                paddingLeft: 8,
                borderRadius: 6,
              }}
            >
              Frontend Developer
            </h1>

            <h2
              style={{
                borderLeft: "2px solid #222222",
                padding: 8,
              }}
            >
              Chita Cătălin-Adrian
            </h2>
          </div>

          <div
            style={{
              zIndex: 20000,
            }}
          >
            <div style={{ marginBottom: 4 }}>
              Random snippets of code i've written{" "}
            </div>
            <Progress
              strokeColor={{
                "0%": "#333333",
                "100%": "#87d068",
              }}
              percent={percent}
              status="active"
              showInfo={false}
            />
            <div style={{ width: 300, maxHeight: 300 }}>
              <SyntaxHighlighter language="javascript" style={a11yDark}>
                {CodeArray[selectedCode]}
              </SyntaxHighlighter>
            </div>
          </div>
        </div>

        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "flex-start",
            flexGrow: 1,
            marginTop: 16,
          }}
        >
          <a
            href="https://www.linkedin.com/in/c%C4%83t%C4%83lin-adrian-chita-0b9345160/"
            style={{ zIndex: 9999 }}
          >
            <LinkedinOutlined
              style={{ color: "#222222", fontSize: "2em", marginRight: 8 }}
            />
          </a>

          <a
            href="https://www.facebook.com/catalinadrian.chita.1"
            style={{ zIndex: 9999 }}
          >
            <FacebookOutlined
              style={{ color: "#222222", fontSize: "2em", marginRight: 8 }}
            />
          </a>

          <a
            href="https://www.instagram.com/catalin_chita/?hl=en"
            style={{ zIndex: 9999 }}
          >
            <InstagramOutlined style={{ color: "#222222", fontSize: "2em" }} />
          </a>
        </div>
      </div>
      <div
        style={{
          color: "grey",
          zIndex: 9999,
          display: "flex",
          justifyContent: "flex-end",
          width: "95vw",
          padding: 8,
        }}
      >
        <a
          href="https://vincentgarreau.com/particles.js/"
          style={{ textDecoration: "none" }}
        >
          Particle.js
        </a>
      </div>
    </div>
  );
}

export default App;
